import React, { Component } from 'react';
import { connect } from 'react-redux';

import ListAll from './snippet/list-all';
import ListBasic from './snippet/list-basic';
import ListBenhNhan from './snippet/list-benhnhan';
import ListViPham from './snippet/list-vipham';
import ReportAll from './snippet/report-all';
import ReportDone from './snippet/report-done';
import ReportWaiting from './snippet/report-waiting';
import SendNoteAll from './snippet/send-note-all';
import SendNoteBacsi from './snippet/send-note-bacsi';
import SendNoteNguoidungthuong from './snippet/send-note-nguoidungthuong';
import SendNoteLuaChon from './snippet/send-note-luachon';
import StatisticalLuuLuong from './snippet/statistical-luuluong';
import StatisticalThuChi from './snippet/statistical-thuchi';



import '../css/snippet/list-all.css'
import '../css/snippet/list-basic.css'
import '../css/snippet/list-benhnhan.css'
import '../css/snippet/list-vipham.css'
import '../css/snippet/report-all.css'
import '../css/snippet/report-done.css'
import '../css/snippet/report-waiting.css'
import '../css/snippet/send-note-all.css'
import '../css/snippet/send-note-bacsi.css'
import '../css/snippet/send-note-nguoidungthuong.css'
import '../css/snippet/send-note-luachon.css'
import '../css/snippet/statistical-luuluong.css'
import '../css/snippet/statistical-thuchi.css'

class MainContent extends Component {

  constructor(props) {
    super(props);
    this.state = {}
  }
  nav() {
    const { currentId } = this.props;
    switch (currentId) {
      case 'list-all':
        return <ListAll />
      case 'list-doctor':
        return <ListBasic />
      case 'list-vipham':
        return <ListViPham />
      case 'report-all':
        return <ReportAll />
      case 'send-noti-all':
        return <SendNoteAll />
      default:
        return null;
    }
  }
  render() {
    return (
      <div className="block-main-content position-absolute">
        {
          this.nav()
        }
      </div>
    );
  };

}
const mapStateToProps = state => ({ currentId: state.nav.id });

export default connect(mapStateToProps)(MainContent);



