import React, {Component} from 'react';

export default class ListAll extends Component {


  constructor(props) {
    super(props);
    this.state = {
      sortByType: {
        sortBy: 0,    //0 => by id, 1: name, 2: status Lock, 3: by status Ticked //column of table
        sortIdUp: true,
        sortNameAToZ: true,
        sortStatusLock: true,
        sortStatusTick: true,
      },
      userList: [
        {
          id: "id con gà",
          name: "Một con gà",
          detail: "có 2 chân",
          lock: false,
          tick: false,
        },
        {
          id: "id con heo",
          name: "Một con heo",
          detail: "có 4 chân",
          lock: false,
          tick: true,
        },
        {
          id: "id con chó",
          name: "Một con chó",
          detail: "có 4 chân",
          lock: true,
          tick: false,
        },
        {
          id: "id con mèo",
          name: "Một con mèo",
          detail: "có 4 chân",
          lock: true,
          tick: true,
        },
      ]
    }
  }

  setSortById() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 0;
    sortBy.sortIdUp = !sortBy.sortIdUp;
    this.setState ({
      sortByType: this.state.sortByType,
    })
  }
  setSortByName() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 1;
    sortBy.sortNameAToZ = !sortBy.sortNameAToZ;
    this.setState ({
      sortByType: this.state.sortByType,
    })
  }
  setSortByLock() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 2;
    sortBy.sortStatusLock = !sortBy.sortStatusLock;
    this.setState ({
      sortByType: this.state.sortByType,
    })
  }
  setSortByTick() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 3;
    sortBy.sortStatusTick = !sortBy.sortStatusTick;
    this.setState ({
      sortByType: this.state.sortByType,
    })
  }

  toggle_tick_account(i) {
    const user = this.state.userList[i];
    user.tick = !user.tick;
    this.setState({
      userList: this.state.userList,
    });
  };

  toggle_lock_account(e, i) {
    e.stopPropagation();
    const user = this.state.userList[i];
    user.lock = !user.lock;
    var x = window.confirm((user.lock) ? ("LOCK ACCOUNT") : ("UNLOCK ACCOUNT: ") + user.id);
    if (x) {
      this.setState({
        userList: this.state.userList,
      });
    }
    else {

    }
  };

  render() {
    return (
      <div className="list-all">
        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title">
              <span>
                Danh sách người dùng
              </span>
            </div>
            <div className="block-content-body">
              <div className="list-user">
                <table className="table table-hover">
                  <thead className="thead-dark">
                  <tr className="">
                    <th onClick={() => this.setSortById()}>Id
                      <span className="float-right">
                        <i className={"fas " + ((this.state.sortByType.sortIdUp) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                    </th>
                    <th onClick={() => this.setSortById()}>Họ và tên
                      <span className="float-right">
                        <i className={"fas " + ((this.state.sortByType.sortNameAToZ) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                    </th>
                    <th>Mô tả</th>
                    <th></th>
                    <th className="text-center" onClick={() => this.setSortById()}>Khóa tài khoản
                      <span className="float-right">
                        <i className={"fas " + ((this.state.sortByType.sortStatusLock) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                    </th>
                    <th className="text-center" onClick={() => this.setSortById()}>Đánh dấu
                      <span className="float-right">
                        <i className={"fas " + ((this.state.sortByType.sortStatusTick) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                    </th>
                  </tr>
                  </thead>
                  <tbody>
                  {
                    this.state.userList.map((elm, i) => (
                      <tr key={i} onClick={() => this.toggle_tick_account(i)}>
                        <td>{elm.id}</td>
                        <td>{elm.name}</td>
                        <td>{elm.detail}</td>
                        <td className="d-flex justify-content-around text-center">
                          <span title="Chi tiết"><i className="fas fa-info-circle"></i></span>
                          <span title="Các bài viết"><i className="fas fa-th-list"></i></span>
                        </td>
                        <td className="text-center">
                          {
                            elm.lock ?
                              (
                                <span title="Tài khoản đang khóa" onClick={(e) => this.toggle_lock_account(e, i)}
                                  className="icon-lock">
                                  <i className="fas fa-lock" />
                                </span>
                              ) :
                              (
                                <span title="Tài khoản đang mở" onClick={(e) => this.toggle_lock_account(e, i)}
                                  className="icon-unlock">
                                  <i className="fas fa-lock-open" />
                                </span>
                              )
                          }
                        </td>
                        <td>
                          <span className="d-flex justify-content-center">
                            <input type="checkbox" className="m-0" checked={elm.tick} onChange={() => {}} />
                          </span>
                        </td>
                      </tr>
                    ))
                  }
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

}


